define("appkit/views/application",
  [],
  function() {
    "use strict";
    var ApplicationView = Ember.View.extend({
      classNames: ['application-view'],
      elementId: 'application-view',

      setSnapPoint: function() {
        this.$("#main").snapPoint({
          scrollDelay: 200,
          scrollSpeed: 100,
          outerTopOffset: 700,
          innerTopOffset: 200
        });
      }.on('didInsertElement')

    });




    return ApplicationView;
  });