define("appkit/routes/cards/latest",
  [],
  function() {
    "use strict";
    var LatestCardsRoute = Ember.Route.extend({
      model: function() {
        return this.store.filter('card', function(card) {
          return !!card.get('isLive');
        });
      },
    });


    return LatestCardsRoute;
  });